import React, { useState, useEffect } from "react";
import Data from "./Data";

function App() {
  const [sound, setSound] = useState(Data);
  const [name, setName] = useState("");

  useEffect(() => {
    document.addEventListener("keypress", handleClick);
  });
  const handleClick = (e) => {
    let key = e.key;
    if (key) {
      const newKey = key.toUpperCase();
      const Clicksound = sound.filter((item) => item.title === newKey);
      if (Clicksound.length > 0) {
        const title = Clicksound[0].id;
        setName(title);
        const SingleSound = new Audio(Clicksound[0].soundUrl);
        SingleSound.play();
      }
    } else {
      const nodeName = e.target.id;
      setName(nodeName);
      const Clicksound = sound.filter((item) => item.id === nodeName);
      const title = Clicksound[0].id;
      setName(title);
      const SingleSound = new Audio(Clicksound[0].soundUrl);
      SingleSound.play();
    }
  };

  const single = sound.map((item) => {
    const Style = {
      backgroundColor: name === item.id ? "#102a42" : "#2680c0",
    };
    return (
      <button
        className="drum-pad"
        id={item.id}
        style={Style}
        onClick={handleClick}
      >
        <audio className="clip" src={item.soundUrl} id={item.title}></audio>
        {item.title}
      </button>
    );
  });

  return (
    <section id="drum-machine">
      <div id="display">
        <div className="name">{name}</div>
        <div className="button">{single}</div>
      </div>
    </section>
  );
}

export default App;
